body {
  border: 10px #000;
  grid-template: 12.5% 12.5% 9.375% 9.375% 9.375% 9.375% 9.375% 9.375% 9.375% 9.375% / 20% 20% 20% 20% 20%;
  width: 100vw;
  height: 100vh;
  padding: 10px;
  display: grid;
}

#defaultCity {
  color: #000;
  background-color: #ffb6c1;
  grid-row: 9 / 10;
}

#cityinput {
  grid-row: 8 / 9;
}

#real-time-temp {
  color: #000;
  background-color: #ffb6c1;
  grid-row: 5 / 6;
}

#temp {
  text-align: center;
  grid-row: 6 / 7;
  font-family: Lucida Sans, Lucida Sans Regular, Lucida Grande, Lucida Sans Unicode, Geneva, Verdana, sans-serif;
  font-size: xx-large;
}

#up {
  color: #000;
  background-color: #e9c0fa;
  grid-area: 5 / 2 / 6 / 3;
}

#down {
  color: #000;
  background-color: #e9c0fa;
  grid-area: 9 / 2 / 10 / 3;
}

#temperature-now {
  text-align: center;
  grid-area: 6 / 2 / 7 / 3;
  font-size: 400%;
}

#sky {
  text-align: center;
  grid-area: 6 / 4 / 7 / 5;
  font-size: xx-large;
}

#sky-drop-down {
  background-color: #add8e6;
  grid-area: 5 / 4 / 6 / 5;
}

#weather-garden {
  text-align: center;
  grid-area: 9 / 4 / 10 / 5;
  font-size: xx-large;
}

#header_id2 {
  text-align: center;
  grid-area: 2 / 2 / 3 / 5;
  font-family: Lucida Sans, Lucida Sans Regular, Lucida Grande, Lucida Sans Unicode, Geneva, Verdana, sans-serif;
  font-size: xx-large;
}

#header_id1 {
  text-align: center;
  grid-area: 1 / 2 / 2 / 5;
  font-family: Lucida Sans, Lucida Sans Regular, Lucida Grande, Lucida Sans Unicode, Geneva, Verdana, sans-serif;
  font-size: xx-large;
}

#city {
  background-color: #add8e6;
}
